import { Component } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { CoreProvider } from 'src/app/services/core';

@Component({
  selector: 'app-product-detail',
  templateUrl: 'product-detail.page.html',
  styleUrls: ['product-detail.page.scss']
})
export class ProductDetailPage {

  defaultHref = "/home";
  idProduct = null;

  get product() {
    if (!this.idProduct == null) return null;
    let res = false;
    this.data.Products.forEach(itm => {
      if (itm[0].val.id == this.idProduct) res = itm;
    });
    return res;
  }

  get data() {
    return this.core.data.data;
  }
  get editMode() {
    return this.core.data.editMode;
  }

  constructor(public core: CoreProvider, activatedRoute: ActivatedRoute) {
    const id = activatedRoute.snapshot.params.id;
    if (!id) {
      this.core.navCtrl.navigateBack(this.defaultHref);
    } else {
      this.idProduct = id;
    }
  }

  edit = () => this.core.data.edit();
  saveEdit = () => this.core.data.saveEdit();
  cancelEdit = () => this.core.data.cancelEdit();

}
